import { Component, OnInit, Input, Pipe, PipeTransform } from '@angular/core';
import { ChartDataVm } from '../viewmodel/chart-data-vm';
import { DataSetVm } from '../viewmodel/data-set-vm';
import { General } from './re-usable/general-function';


@Component({
  selector: 'table-viewer',
  template: require('./table-viewer.component.html'),
  styles: [require('./table-viewer.component.css')]
})
export class TableViewerComponent implements OnInit {

  @Input('tableModel') tableChart: ChartDataVm;

 
  tableData: any;
  realData = new Array<any>();

  tableLabels = new Array<any>();

  constructor() { }

  ngOnInit() {
    this.tableLabels.push({label:"Title"})
    this.tableChart.labels.forEach(element => {
      this.tableLabels.push({label:element})
    });
    var rows = new Array<any>();

    for (let y of this.tableChart.datasets) {
      var row: any = {};
      Object.defineProperty(row, this.tableLabels[0].label, {
        value: y.label,
        writable: true,
        enumerable: true,
        configurable: true
      });
      for (var j = 0; j < y.data.length; j++) {

        Object.defineProperty(row, this.tableChart.labels[j], {
          value: y.data[j],
          //      value: this.transform(Number(y.data[j].toString())),
          writable: true,
          enumerable: true,
          configurable: true
        });

      }
      rows.push(row);

    }

    // this is to instantiate the real object that is of type "any" not Array<any> that would be sent to the html
    this.realData = [({ labels: this.tableLabels, rows: rows })];
    this.tableData = this.realData[0];
  }

  downloadFormat() {
    this.download();
  }
  download() {
    var csvData = General.ConvertToCSV(this.realData[0].rows);
    var a = document.createElement("a");
    a.setAttribute('style', 'display:none;');
    document.body.appendChild(a);
    var blob = new Blob([csvData], { type: 'text/csv' });
    var url = window.URL.createObjectURL(blob);
    a.href = url;
    a.download = this.tableChart.caption + ".csv";
    a.click();
  }
  transform(value: any): any {
    
      
        return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',') ;
      
       
  }
}



