import { Component, OnInit, ElementRef, ViewChild, Input } from '@angular/core';
import { DimensionVm } from '../viewmodel/dimension-vm';
import { ChartType } from '../models/chart-type';
import { ChartDataVm } from '../viewmodel/chart-data-vm';
import { DataSetVm } from '../viewmodel/data-set-vm';

import {Connection} from "./Connection";
import { BandsComponent } from './bands.component';
import { BIService } from '../../services/bi-chart-viewer';
import { ChartData } from '../index';

@Component({
    selector: 'bi-viewer',
    template: require('./bi-chart-viewer.component.html'),
    styles: [require('./bi-chart-viewer.component.css')]
})
export class BIViewerComponent implements OnInit {
    @Input('wsUrl') wsUrl:string;
    @Input('url') url:string;
   // @Input('profile') profile:string;
    connection: any;
    profileBands: any;
    profileTitle: string;
    group: string = "";
    chartDatas: BandsComponent[]=[];
    @ViewChild('bandsContainer') bandsContainer: ElementRef;

    me: BIViewerComponent;
    constructor(private cService: BIService) {
        cService.url=this.url;
    }

    ngOnInit() {
        console.log(this);
        this.me = this;
        this.cService.url = this.url;
        this.cService.wsUrl = this.wsUrl;
       
    }
}


