import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { Observable } from "rxjs/Observable";
import { Http, Response, Headers, RequestOptions } from "@angular/http";

import * as go from 'gojs';
import { ChartDataVm, ProfileStructureVm, ChartData, Connection } from '../components/index';
import { BandsComponent } from '../index';

@Injectable()
export class BIService {
    url: string;
    wsUrl: string;
    public diagram: go.Diagram = new go.Diagram();
    profileBands: any;
    profileTitle: any;
    group: string = "";
    chartDatas: BandsComponent[] = [];
    connection: any;

    protected options: RequestOptions;
    protected headers = new Headers;
    // url = 'http://50.97.89.9:66/api/';
    constructor(private http: Http) {
        this.headers.append('Content-Type', 'application/json');
        // this.headers.append('Access-Control-Allow-Origin', '*');
        // this.headers.append("Access-Control-Allow-Headers", "X-Requested-With");
        this.options = new RequestOptions({
            headers: this.headers
        });
    }

    public GetChartData(profile: string, chart: string): Observable<ChartDataVm> {

        return this.http.get(this.url + "render/GetChartData/" + profile + "/" + chart)
            .map((response: Response) =>
                <ChartDataVm>response.json()
            )
            .catch(this._serverError);
    }
    public _serverError(err: any): Observable<any> {
        console.log('server error:', err);  // debug
        if (err instanceof Response) {
            return Observable.throw(err.json().error || 'backend server error');

        }
        return Observable.throw(err || 'backend server error');
    }
    public GetProfile(profile: string): Observable<any> {
        const url = this.url + 'render/GetProfile/' + profile;
        console.log(url);
        return this.http.get(url)
            .map((response: Response) =>
                <any>response.json()
            )
            .catch(this._serverError);
    }

    public GetProfileStructure(): Observable<any> {
        const url = this.url + 'Profile';
        console.log(url);
        return this.http.get(url)
            .map((response: Response) =>
                <any>response.json()
            )
            .catch(this._serverError);
    }

    public AddProfileStructure(newProfile: ProfileStructureVm): Observable<ProfileStructureVm[]> {
        const url = this.url + 'Profile';
        let toAdd = JSON.stringify(newProfile);
        return this.http
            .post(url, toAdd, { headers: this.headers })
            .map((response: Response) => <ProfileStructureVm[]>response.json())
            .catch(this._serverError);
    }

    deleteProfileStructure(title: string): Observable<ProfileStructureVm[]> {
        const url = this.url + 'Profile/' + title;
        return this.http
            .post(url, { headers: this.options.headers })
            .map((response: Response) => <ProfileStructureVm[]>response.json())
            .catch(this._serverError);
    }

    public GetEntities(): Observable<any> {
        const url = this.url + 'structure/getentities/';
        return this.http.get(url)
            .map((response: Response) =>
                <any>response.json()
            )
            .catch(this._serverError);
    }
    public GetConnections(entity: string[]): Observable<any> {
        const url = this.url + 'structure/getconnections/';
        let EtoAdd = JSON.stringify(entity);
        return this.http
            .post(url, EtoAdd, { headers: this.options.headers })
            .map((response: Response) =>
                <any>response.json()
            )
            .catch(this._serverError);
    }

    loadProfile(profile: string) {

        this.GetProfile(profile).subscribe((p => {
            if (p) {
                this.group = "";
                this.profileBands = p.bands;
                this.profileTitle = p.title;
                p.entities.forEach(element => {
                    this.group += "/" + element;
                });
                if (this.wsUrl) {
                    this.InitAutoRefresh(this.group);                    
                }
            }
        }))
        this.profileBands = null;
        this.profileTitle = null;
    }

    replaceChartData(data: ChartData[]) {
        data.forEach(d => {
            this.chartDatas.forEach((bd: BandsComponent) => {
                var i = 0;
                bd.band.dimensions.forEach(cd => {
                    if (cd.caption == d.caption) {
                        bd.band.dimensions.splice(i, 1, (d));
                    }
                    i++;
                });
            });
        });

    }
    public InitAutoRefresh(groups: string) {
        this.connection = new Connection(this.wsUrl + groups); //'ws://localhost:54764/quil/[a@y.com]'
        this.connection.clientMethods["refreshChart"] = (message: any) => {
            this.replaceChartData(message.dimensions);
        };
        this.connection.start();
    }
}


