import { ChartData } from "../models/chart-data";
import { DataSetVm } from "./data-set-vm";
import { DataSet } from "../models/data-set";

export class ChartDataVm implements ChartData {
    labels = new Array<string>();
    datasets = new Array<DataSet>();
    entities = new Array<any>();
    caption: string;
    subValue: string;
    footer: string;
    mainValue: any;
    type: string;
    backgroundColor: string;
    foreColor: string;
}


