import { Component, OnInit, Input } from '@angular/core';
import { ChartDataVm } from '../viewmodel/chart-data-vm';
import { DataSetVm } from '../viewmodel/data-set-vm';

@Component({
  selector: 'chart-viewer',
  template: require('./chart-viewer.component.html'),
  styles: [require('./chart-viewer.component.css')]
})
export class ChartViewerComponent implements OnInit {
  @Input('barModel')  barDatas: ChartDataVm;
  @Input('lineModel') lineDatas: ChartDataVm;
  @Input('pieModel')  pieDatas: ChartDataVm;
  @Input('douModel')  douDatas: ChartDataVm;
  
  lineData = new ChartDataVm();  
  barData = new ChartDataVm();
  pieData = new ChartDataVm();
  douData = new ChartDataVm();
  
  constructor() { }

  ngOnInit() {
    var dw=this.barDatas;
   }

}



