import { Component, OnInit, Input } from '@angular/core';
import { ViewBandVm } from '../viewmodel/view-band-vm';
import { DimensionVm } from '../viewmodel/dimension-vm';
import { ChartType } from '../models/chart-type';
import { ChartDataVm } from '../viewmodel/chart-data-vm';
import { DataSetVm } from '../viewmodel/data-set-vm';
import { BIViewerComponent } from './bi-chart-viewer.component';
import { BIService } from '../../services/bi-chart-viewer';




@Component({
  selector: 'bands',
  template: require('./bands.component.html'),
  styles: [require('./bands.component.css')]
})
export class BandsComponent implements OnInit {

  band = new ViewBandVm();

  @Input('model') pBand;
  @Input('caption') pTitle;
  @Input('parent') parent: BIViewerComponent;
  constructor(private cService: BIService) {
    console.log('Get Chart complete');
  }
  checkifLbl(dim: ChartDataVm): boolean {
    return dim.type.toString() == "Label";
  }

  checkifBar(dim: ChartDataVm): boolean {
    return dim.type.toString() == "Bar";
  }

  checkifLine(dim: ChartDataVm): boolean {
    return dim.type.toString() == "Line";
  }

  checkifPie(dim: ChartDataVm): boolean {
    return dim.type.toString() == "Pie";
  }

  checkifDou(dim: ChartDataVm): boolean {
    return dim.type.toString() == "Doughnut";
  }

  checkifTable(dim: ChartDataVm): boolean {
    return dim.type.toString() == "Table";
  }


  ngOnInit() {

    this.pBand.charts.forEach(element1 => {
      this.cService.GetChartData(this.pTitle, element1.title).subscribe((k => {
        this.band.dimensions.push(k);
        if (this.parent) {
          this.parent.chartDatas.push(this);
        }
      }),
        error => { console.log(error) },
        () => { console.log('Get Chart complete'); }
      )
    });
  }

}



