import { Component, OnInit, ViewChild, ElementRef, Input, Output, EventEmitter } from '@angular/core';
import * as go from 'gojs';
import { BIService } from '../../../services/bi-chart-viewer';

@Component({
  selector: 'app-diagram-editor',
  template: require('./diagram-editor.component.html'),
  styles: [require('./diagram-editor.component.css')]
})
export class DiagramEditorComponent implements OnInit {
  // public diagram: go.Diagram = new go.Diagram();
  
  @ViewChild('diagramDiv')
  private diagramRef: ElementRef;

  @Input()
  get model(): go.Model { return this.service.diagram.model; }
  set model(val: go.Model) { this.service.diagram.model = val; }

  @Output()
  nodeSelected = new EventEmitter<go.Node | null>();

  @Output()
  modelChanged = new EventEmitter<go.ChangedEvent>();

  constructor(private service:BIService) {
    
    const $ = go.GraphObject.make;
    this.service.diagram = new go.Diagram();
    this.service.diagram.initialContentAlignment = go.Spot.Center;
    this.service.diagram.undoManager.isEnabled = true;
    this.service.diagram.addDiagramListener("ChangedSelection",
      e => {
        const node = e.diagram.selection.first();
        this.nodeSelected.emit(node instanceof go.Node ? node : null);
      });
    this.service.diagram.addModelChangedListener(e => e.isTransactionFinished && this.modelChanged.emit(e));
    var lightgrad = $(go.Brush, "Linear", { 1: "#E5E5E5", 0: "#FFFFFF" });
    

    var itemTempl =
      $(go.Panel, "Horizontal",
        $(go.Shape,
          { desiredSize: new go.Size(10, 10) },
          new go.Binding("figure", "figure"),
          new go.Binding("fill", "color")),
        $(go.TextBlock,
          {
            margin: new go.Margin(5, 0, 0, 5),
      //      editable: true,
            stroke: "#333333",
            font: "bold 14px sans-serif"
          },
          new go.Binding("text", "name"))
      );

    this.service.diagram.nodeTemplate =
      $(go.Node, "Auto",
        {
          minSize: new go.Size(180, 150),
          selectionAdorned: true,
          resizable: true,
          layoutConditions: go.Part.LayoutStandard & ~go.Part.LayoutNodeSized,
          fromSpot: go.Spot.AllSides,
          toSpot: go.Spot.AllSides,
          isShadowed: true,
          shadowColor: "#C5C1AA"
        },
        new go.Binding("location", "loc", go.Point.parse).makeTwoWay(go.Point.stringify),
        $(go.Shape, "Rectangle",
          {
            fill: lightgrad, stroke: "#756875", strokeWidth: 3,

          }),
        $(go.Panel, "Table",
          { margin: 8, stretch: go.GraphObject.Fill },
          $(go.RowColumnDefinition, { row: 0, sizing: go.RowColumnDefinition.None }),
          new go.Binding("fill", "color")),
        $(go.TextBlock, // header
          {
      //      editable: true,
            row: 0, alignment: go.Spot.TopCenter,
            margin: new go.Margin(5, 14, 10, 2),  // leave room for Button
            font: "bold 16px sans-serif"
          },
          new go.Binding("text", "key")),
        // $("PanelExpanderButton", "LIST",  // the name of the element whose visibility this button toggles
        //   { row: 0, alignment: go.Spot.TopRight }),
        $(go.Panel, "Vertical",
          {
            name: "LIST",
            row: 1,
            padding: 3,
            alignment: go.Spot.Center,
            defaultAlignment: go.Spot.Left,
            stretch: go.GraphObject.Horizontal,
            itemTemplate: itemTempl
          },
          new go.Binding("itemArray", "items"))
      );  // end Table Panel
    // );

    this.service.diagram.linkTemplate =
      $(go.Link,
        // allow relinking
        {
          selectionAdorned: true,
          layerName: "Foreground",
          reshapable: true,
          routing: go.Link.AvoidsNodes,
          corner: 5,
          curve: go.Link.JumpOver
        },
        $(go.Shape,  // the link shape
          { stroke: "#303B45", strokeWidth: 2.5 }),
        $(go.TextBlock,  // the "from" label
          {
            editable: true,
            textAlign: "center",
            font: "bold 14px sans-serif",
            stroke: "#1967B3",
            segmentIndex: 0,
            segmentOffset: new go.Point(NaN, NaN),
            segmentOrientation: go.Link.OrientUpright
          },
          new go.Binding("text", "text")),
        $(go.TextBlock,  // the "to" label
          {
            editable: true,
            textAlign: "center",
            font: "bold 14px sans-serif",
            stroke: "#1967B3",
            segmentIndex: -1,
            segmentOffset: new go.Point(NaN, NaN),
            segmentOrientation: go.Link.OrientUpright
          }, new go.Binding("text", "toText"))
      );
  }

  ngOnInit() {
    this.service.diagram.div = this.diagramRef.nativeElement;
  }
}



