import { Component,
    Input,
    Output,
    ElementRef,
    EventEmitter,
    ViewChild,
    Renderer,
    forwardRef,
    OnInit } from '@angular/core';
  import {ControlValueAccessor, NG_VALUE_ACCESSOR} from '@angular/forms';
  
  const INLINE_EDIT_CONTROL_VALUE_ACCESSOR = {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => InputLabelComponent),
    multi: true
  };
  
  @Component({
    selector: 'input-lbl',
    template: require('./input-label.component.html'),
    providers: [INLINE_EDIT_CONTROL_VALUE_ACCESSOR],
    styles: [require('./input-label.component.css')]
  })
  
  export class InputLabelComponent implements ControlValueAccessor, OnInit {
  
    @ViewChild('inputlabel') inlineEditControl: ElementRef; // input DOM element
    @Input() label: string = '';  // Label value for input element
    @Input() type: string = 'text'; // The type of input element
    @Input() required: boolean = false; // Is input requried?
    @Input() disabled: boolean = false; // Is input disabled?
    //  @Input() editable: boolean = true; // Is input disabled?
    @Output('blur') blur: EventEmitter<any> = new EventEmitter<any>();// Is input lost focus?
    // @Output('valueChange')valueModified:EventEmitter<any> = new EventEmitter<any>();// Is input lost focus?
    private _value: string = ''; // Private variable for input value
    private preValue: string = ''; // The value before clicking to edit
    private editing: boolean = false; // Is Component in edit mode?
    public onChange: any = Function.prototype; // Trascend the onChange event
    public onTouched: any = Function.prototype; // Trascend the onTouch event
     editCaption="Click to edit";
    // Control Value Accessors for ngModel
    get value(): any {
      return this._value;
    }
  
    set value(v: any) {
      if (v !== this._value) {
        this._value = v;
        this.onChange(v);
      }
    }
  
    constructor(element: ElementRef, private _renderer: Renderer) {
    
    }
  
  
  
    // Required for ControlValueAccessor interface
    writeValue(value: any) {
      this._value = value;
    }
  
    // Required forControlValueAccessor interface
    public registerOnChange(fn: (_: any) => {}): void {
      this.onChange = fn;
      // this.valueModified.next()
    }
  
    // Required forControlValueAccessor interface
    public registerOnTouched(fn: () => {}): void {
      this.onTouched = fn;
    }
  
    // Do stuff when the input element loses focus
    onBlur(event: Event) {
      this.editing = false;
      this.blur.next(event);
    }
  
    // Start the editting process for the input element
    edit(value) {
      if (this.disabled) {
        return;
      }
  
      this.preValue = value;
      this.editing = true;
      // Focus on the input element just as the editing begins
     setTimeout(_ =>  this._renderer.invokeElementMethod(this.inlineEditControl.nativeElement, 'focus'));
    }
  
    ngOnInit() {
        if(this.disabled)
        this.editCaption='';
    }
  }


