@Pipe({ name: 'safeHtml' })
export class Safe implements PipeTransform {
  constructor(private sanitized: DomSanitizer) { }
  transform(value) {

    return value = value.slice(0, -4);
  }
}

@Pipe({ name: 'formatOp' })
export class Format implements PipeTransform {
  constructor(private sanitized: DomSanitizer) { }
  transform(value) {
    if (value === "Add")
      value = "+";
    if (value === "Multiply")
      value = "x";
    if (value === "Subtract")
      value = "-";
    if (value === "Divide")
      value = "/";
    return value;
  }
}


